import { useStaticQuery } from 'gatsby'
import React from 'react'
import { StyledSection } from '../../commonStyled'
import { LinkWrapper } from './style'
import { graphql } from 'gatsby'

export default function Contact() {
  const { site : { siteMetadata } } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            github
            linkedin
            email
            resume
          }
        }
      }
    `
  )
  return (
    <StyledSection id="section-contact" vh="10">
      <LinkWrapper>
        {
          Object.entries(siteMetadata).map(([key, value]) => {
            return (
              <a key={key} href={value} rel="noreferrer" target="_blank">{key}</a>
            )
          })
        }
      </LinkWrapper>
    </StyledSection>
  )
}
