import React from 'react'
import { StyledSection } from '../../commonStyled'
import { ProjectContainer, ProjectGridWrapper, ProjectTitle } from './style'
import Project1 from '../../../content/projects/project-1.mdx'
import Project2 from '../../../content/projects/project-2.mdx'
import Project3 from '../../../content/projects/project-3.mdx'

import { useStaticQuery, graphql } from "gatsby"

export default function Projects() {
  const data = useStaticQuery(graphql`
  query {
    project1: file(relativePath: { eq: "images/chatterbox.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    project2: file(relativePath: { eq: "images/Astar-visualisation.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    project3: file(relativePath: { eq: "images/GatsbyCms.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
  `)
  
  return (
    <StyledSection id="section-projects">
      <ProjectContainer>
        <ProjectTitle>
          Projects
        </ProjectTitle>
        <ProjectGridWrapper>
          <Project1 img={data?.project1?.childImageSharp?.fluid}/>
          <Project2 img={data?.project2?.childImageSharp?.fluid}/>
          <Project3 img={data?.project3?.childImageSharp?.fluid}/>
          {/* <Project4/> */}
        </ProjectGridWrapper>
      </ProjectContainer>
    </StyledSection>
  )
}

